.Toolbar {
    height:56px;
    width:100%;
    position: fixed;
    overflow: hidden;
    top: 0;
    left: 0;
    /* background-color: #703B09; */
    background-color:white;  
    border-bottom: 1px solid #703B09 ;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav {
    height: 100%;

}

.Logo {
    height: 100%; 
}

@media (max-width:499px) {
    .DesktopOnly {
        display: none;
    }
}